import { useState, useEffect } from "react";
import axios from "../utils/axiosConfig";
import {
  Container,
  Row,
  Col,
  Form,
  Button,
  Card,
  Alert,
  Image,
} from "react-bootstrap";
import ReactStars from "react-rating-stars-component"; // Updated import
import io from "socket.io-client";
import { useNavigate } from "react-router-dom";

const socket = io(process.env.REACT_APP_API_URL || "http://localhost:5000");

function Profile() {
  const [user, setUser] = useState(null);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [picture, setPicture] = useState(null);
  const [headline, setHeadline] = useState("");
  const [portfolio, setPortfolio] = useState([
    { title: "", image: "", description: "" },
    { title: "", image: "", description: "" },
  ]);
  const [links, setLinks] = useState({
    portfolio: "",
    socialMedia: { twitter: "", linkedin: "", instagram: "" },
    email: "",
  });
  const [contact, setContact] = useState({ address: "", phone: "" });
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState("");
  const [isOnline, setIsOnline] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchProfile = async () => {
      try {
        const res = await axios.get("/api/auth/profile", {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        });
        setUser(res.data);
        setHeadline(res.data.headline || "");
        setPortfolio(
          res.data.portfolio.length ? res.data.portfolio : portfolio
        );
        setLinks(res.data.links || links);
        setContact(res.data.contact || contact);
        setIsOnline(res.data.isOnline);
      } catch (err) {
        setError("Failed to load profile");
      }
    };
    fetchProfile();

    socket.emit("setOnline", localStorage.getItem("userId"));
    socket.on("statusUpdate", ({ userId, isOnline }) => {
      if (userId === user?._id) setIsOnline(isOnline);
    });

    return () => socket.emit("setOffline", localStorage.getItem("userId"));
  }, []);

  const handlePictureUpload = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("picture", picture);
    try {
      const res = await axios.post("/api/users/upload-picture", formData, {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
      });
      setSuccess(res.data.message);
      setUser({ ...user, picture: res.data.picture });
    } catch (err) {
      setError("Failed to upload picture");
    }
  };

  const handleProfileUpdate = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.put(
        "/api/users/profile",
        { headline, portfolio, links, contact },
        {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        }
      );
      setSuccess("Profile updated");
      setUser(res.data);
    } catch (err) {
      setError("Failed to update profile");
    }
  };

  const handleRating = async (e) => {
    e.preventDefault();
    try {
      await axios.post(
        `/api/users/rate/${user._id}`,
        { score: rating, comment },
        {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        }
      );
      setSuccess("Rating submitted");
      setRating(0);
      setComment("");
    } catch (err) {
      setError(err.response?.data?.message || "Failed to submit rating");
    }
  };

  if (!user) return <div>Loading...</div>;

  return (
    <Container className="py-5">
      <Row>
        <Col md={3}>
          <div className="sidebar">
            <h4>Status</h4>
            <div style={{ color: isOnline ? "green" : "red" }}>
              {isOnline ? "Online" : "Offline"}
            </div>
          </div>
        </Col>
        <Col md={9}>
          <h2>{user.name}'s Profile</h2>
          {error && <Alert variant="danger">{error}</Alert>}
          {success && <Alert variant="success">{success}</Alert>}
          <Row>
            <Col md={4}>
              <Image
                src={user.picture || "https://via.placeholder.com/150"}
                roundedCircle
                fluid
              />
              <Form onSubmit={handlePictureUpload} className="mt-3">
                <Form.Group>
                  <Form.Control
                    type="file"
                    onChange={(e) => setPicture(e.target.files[0])}
                  />
                </Form.Group>
                <Button type="submit" className="mt-2">
                  Upload Picture
                </Button>
              </Form>
            </Col>
            <Col md={8}>
              <h4>{user.headline || "No headline set"}</h4>
              <p>Role: {user.role}</p>
              <p>Average Rating: {user.averageRating.toFixed(1)} / 5</p>
            </Col>
          </Row>
          {user.role === "professional" && (
            <>
              <h3>Portfolio</h3>
              <Row>
                {portfolio.map((item, index) => (
                  <Col md={6} key={index}>
                    <Card className="mb-3">
                      <Card.Img
                        variant="top"
                        src={item.image || "https://via.placeholder.com/300"}
                      />
                      <Card.Body>
                        <Card.Title>{item.title || "Untitled"}</Card.Title>
                        <Card.Text>
                          {item.description || "No description"}
                        </Card.Text>
                      </Card.Body>
                    </Card>
                  </Col>
                ))}
              </Row>
              <h3>Links</h3>
              <p>
                Portfolio:{" "}
                <a href={links.portfolio}>{links.portfolio || "N/A"}</a>
              </p>
              <p>
                Twitter:{" "}
                <a href={links.socialMedia.twitter}>
                  {links.socialMedia.twitter || "N/A"}
                </a>
              </p>
              <p>
                LinkedIn:{" "}
                <a href={links.socialMedia.linkedin}>
                  {links.socialMedia.linkedin || "N/A"}
                </a>
              </p>
              <p>
                Instagram:{" "}
                <a href={links.socialMedia.instagram}>
                  {links.socialMedia.instagram || "N/A"}
                </a>
              </p>
              <p>Email: {links.email || "N/A"}</p>
              <h3>Contact</h3>
              <p>Address: {contact.address || "N/A"}</p>
              <p>Phone: {contact.phone || "N/A"}</p>
            </>
          )}
          {user.role === "customer" && (
            <Form onSubmit={handleRating} className="mt-4">
              <h3>Rate Professional</h3>
              <Form.Group>
                <Form.Label>Rating</Form.Label>
                <ReactStars
                  count={5}
                  value={rating}
                  size={24}
                  activeColor="#ffd700"
                  onChange={(newRating) => setRating(newRating)}
                />
              </Form.Group>
              <Form.Group>
                <Form.Label>Comment</Form.Label>
                <Form.Control
                  as="textarea"
                  value={comment}
                  onChange={(e) => setComment(e.target.value)}
                />
              </Form.Group>
              <Button type="submit" className="mt-2">
                Submit Rating
              </Button>
            </Form>
          )}
          <Form onSubmit={handleProfileUpdate} className="mt-4">
            <h3>Update Profile</h3>
            <Form.Group className="mb-3">
              <Form.Label>Headline</Form.Label>
              <Form.Control
                type="text"
                value={headline}
                onChange={(e) => setHeadline(e.target.value)}
              />
            </Form.Group>
            {user.role === "professional" && (
              <>
                {portfolio.map((item, index) => (
                  <div key={index} className="mb-3">
                    <h5>Portfolio Item {index + 1}</h5>
                    <Form.Group>
                      <Form.Label>Title</Form.Label>
                      <Form.Control
                        type="text"
                        value={item.title}
                        onChange={(e) => {
                          const newPortfolio = [...portfolio];
                          newPortfolio[index].title = e.target.value;
                          setPortfolio(newPortfolio);
                        }}
                      />
                    </Form.Group>
                    <Form.Group>
                      <Form.Label>Image URL</Form.Label>
                      <Form.Control
                        type="text"
                        value={item.image}
                        onChange={(e) => {
                          const newPortfolio = [...portfolio];
                          newPortfolio[index].image = e.target.value;
                          setPortfolio(newPortfolio);
                        }}
                      />
                    </Form.Group>
                    <Form.Group>
                      <Form.Label>Description</Form.Label>
                      <Form.Control
                        as="textarea"
                        value={item.description}
                        onChange={(e) => {
                          const newPortfolio = [...portfolio];
                          newPortfolio[index].description = e.target.value;
                          setPortfolio(newPortfolio);
                        }}
                      />
                    </Form.Group>
                  </div>
                ))}
                <h5>Links</h5>
                <Form.Group>
                  <Form.Label>Portfolio URL</Form.Label>
                  <Form.Control
                    type="text"
                    value={links.portfolio}
                    onChange={(e) =>
                      setLinks({ ...links, portfolio: e.target.value })
                    }
                  />
                </Form.Group>
                <Form.Group>
                  <Form.Label>Twitter</Form.Label>
                  <Form.Control
                    type="text"
                    value={links.socialMedia.twitter}
                    onChange={(e) =>
                      setLinks({
                        ...links,
                        socialMedia: {
                          ...links.socialMedia,
                          twitter: e.target.value,
                        },
                      })
                    }
                  />
                </Form.Group>
                <Form.Group>
                  <Form.Label>LinkedIn</Form.Label>
                  <Form.Control
                    type="text"
                    value={links.socialMedia.linkedin}
                    onChange={(e) =>
                      setLinks({
                        ...links,
                        socialMedia: {
                          ...links.socialMedia,
                          linkedin: e.target.value,
                        },
                      })
                    }
                  />
                </Form.Group>
                <Form.Group>
                  <Form.Label>Instagram</Form.Label>
                  <Form.Control
                    type="text"
                    value={links.socialMedia.instagram}
                    onChange={(e) =>
                      setLinks({
                        ...links,
                        socialMedia: {
                          ...links.socialMedia,
                          instagram: e.target.value,
                        },
                      })
                    }
                  />
                </Form.Group>
                <Form.Group>
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="email"
                    value={links.email}
                    onChange={(e) =>
                      setLinks({ ...links, email: e.target.value })
                    }
                  />
                </Form.Group>
                <h5>Contact</h5>
                <Form.Group>
                  <Form.Label>Address</Form.Label>
                  <Form.Control
                    type="text"
                    value={contact.address}
                    onChange={(e) =>
                      setContact({ ...contact, address: e.target.value })
                    }
                  />
                </Form.Group>
                <Form.Group>
                  <Form.Label>Phone</Form.Label>
                  <Form.Control
                    type="text"
                    value={contact.phone}
                    onChange={(e) =>
                      setContact({ ...contact, phone: e.target.value })
                    }
                  />
                </Form.Group>
              </>
            )}
            <Button type="submit" className="mt-2">
              Update Profile
            </Button>
          </Form>
        </Col>
      </Row>
    </Container>
  );
}

export default Profile;
