import { Container, Carousel, Button, Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import { Twitter, Facebook, Instagram } from "react-bootstrap-icons";

function Home() {
  const carouselItems = [
    [
      {
        src: "/images/Plumber.jpg",
        alt: "Plumbing Service",
      },
      {
        src: "/images/Tutor.jpg",
        alt: "Tutoring Service",
      },
      {
        src: "/images/Cleaning.jpg",
        alt: "Cleaning Service",
      },
    ],
    [
      {
        src: "/images/Electrician.jpg",
        alt: "Electrical Service",
      },
      {
        src: "/images/Carpentry.jpg",
        alt: "Carpentry Service",
      },
      {
        src: "/images/haircut.jpg",
        alt: "Haircut Service",
      },
    ],
    [
      {
        src: "/images/Gardeners.jpg",
        alt: "Gardening Service",
      },
      {
        src: "/images/Designers.jpg",
        alt: "Fashion Designing Service",
      },
      {
        src: "/images/Moving.jpg",
        alt: "Moving Service",
      },
    ],
    [
      {
        src: "/images/Photographer.jpg",
        alt: "Photography Service",
      },
      {
        src: "/images/Catering.jpg",
        alt: "Catering Service",
      },
      {
        src: "/images/Trainer.jpg",
        alt: "Personal Training Service",
      },
    ],
  ];

  return (
    <div>
      <Container className="py-5">
        <div className="jumbotron">
          <h1>Welcome to ServiceHub</h1>
          <p>
            Connect with trusted professionals for all your service needs, from
            plumbing to fashion designing.
          </p>
          <Button as={Link} to="/request" variant="primary" size="lg">
            Get Started
          </Button>
        </div>

        <Carousel interval={6000} className="mb-5">
          {carouselItems.map((itemGroup, index) => (
            <Carousel.Item key={index}>
              <div className="carousel-grid">
                {itemGroup.map((item, idx) => (
                  <div key={idx} className="text-center">
                    <img src={item.src} alt={item.alt} className="d-block" />
                    <p className="mt-2">{item.alt}</p>
                  </div>
                ))}
              </div>
            </Carousel.Item>
          ))}
        </Carousel>

        <div className="vision-mission-section">
          <h2 className="text-center mb-4">Our Purpose</h2>
          <Row>
            <Col md={4}>
              <div className="vision-mission-card">
                <h3>Business Statement</h3>
                <p>
                  ServiceHub is dedicated to bridging the gap between customers
                  and skilled professionals, providing a seamless platform for
                  high-quality service delivery.
                </p>
              </div>
            </Col>
            <Col md={4}>
              <div className="vision-mission-card">
                <h3>Vision</h3>
                <p>
                  To be the leading global platform for connecting people with
                  trusted service providers, fostering communities built on
                  reliability and excellence.
                </p>
              </div>
            </Col>
            <Col md={4}>
              <div className="vision-mission-card">
                <h3>Mission</h3>
                <p>
                  We empower customers and professionals by offering a
                  user-friendly, secure, and accessible platform that ensures
                  quality service and builds lasting trust.
                </p>
              </div>
            </Col>
          </Row>
        </div>

        <div className="text-center">
          <h3 className="mb-3">Find the Right Professional Today</h3>
          <p className="mb-4">
            Post a request and get matched with skilled professionals in your
            area.
          </p>
          <Button as={Link} to="/request" variant="primary">
            Post a Request
          </Button>
        </div>
      </Container>

      <footer className="footer">
        <Container>
          <p>
            <Link to="/">Home</Link> | <Link to="/about">About</Link> |{" "}
            <Link to="/contact">Contact</Link>
          </p>
          <p>
            Contact: +2348136340849 | Email:{" "}
            <a href="mailto:info@servicehub.com">info@servicehub.com</a>
          </p>
          <div className="social-icons">
            <a
              href="https://twitter.com/servicehub"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Twitter />
            </a>
            <a
              href="https://facebook.com/servicehub"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Facebook />
            </a>
            <a
              href="https://instagram.com/servicehub"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Instagram />
            </a>
          </div>
          <p className="mt-2">© 2025 ServiceHub. All rights reserved.</p>
        </Container>
      </footer>
    </div>
  );
}

export default Home;
