import { useState, useEffect, useContext } from "react";
import axios from "../utils/axiosConfig";
import { Container, Card, Button, Alert } from "react-bootstrap";
import { AuthContext } from "../context/AuthContext";

function Dashboard() {
  const [requests, setRequests] = useState([]);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const { user } = useContext(AuthContext);

  useEffect(() => {
    const fetchRequests = async () => {
      try {
        const res = await axios.get("/api/requests");
        setRequests(res.data);
      } catch (err) {
        setError(err.response?.data?.message || "Failed to fetch requests");
      }
    };
    fetchRequests();
  }, []);

  const handleRespond = async (requestId) => {
    try {
      const res = await axios.put(`/api/requests/respond/${requestId}`);
      setSuccess(res.data.message);
      setError("");
      // Refresh requests
      const updatedRequests = await axios.get("/api/requests");
      setRequests(updatedRequests.data);
    } catch (err) {
      setError(err.response?.data?.message || "Failed to respond to request");
      setSuccess("");
    }
  };

  return (
    <Container className="py-5" style={{ fontFamily: "Roboto, sans-serif" }}>
      <h2 className="text-center mb-4">Dashboard</h2>
      {error && <Alert variant="danger">{error}</Alert>}
      {success && <Alert variant="success">{success}</Alert>}
      <div>
        {requests.map((request) => (
          <Card key={request._id} className="mb-3 shadow">
            <Card.Body>
              <Card.Title>{request.category}</Card.Title>
              <Card.Text>{request.description}</Card.Text>
              <Card.Text>Status: {request.status}</Card.Text>
              <Card.Text>
                Posted by: {request.userId.name} ({request.userId.email})
              </Card.Text>
              {request.professionalId && (
                <Card.Text>
                  Accepted by: {request.professionalId.name}
                </Card.Text>
              )}
              {user.role === "professional" && request.status === "open" && (
                <Button
                  variant="primary"
                  onClick={() => handleRespond(request._id)}
                >
                  Respond
                </Button>
              )}
            </Card.Body>
          </Card>
        ))}
      </div>
    </Container>
  );
}

export default Dashboard;
