import { Card } from "react-bootstrap";

function Message({ sender, recipient, content, timestamp }) {
  return (
    <Card>
      <Card.Body>
        <Card.Text>
          <strong>From:</strong> {sender} <br />
          <strong>To:</strong> {recipient} <br />
          <strong>Message:</strong> {content} <br />
          <strong>Sent:</strong> {new Date(timestamp).toLocaleString()}
        </Card.Text>
      </Card.Body>
    </Card>
  );
}

export default Message;
