import { useState, useEffect } from "react";
import axios from "../utils/axiosConfig";
import { Container, Form, Button, Alert, ListGroup } from "react-bootstrap";
import Message from "../components/Message";

function Messages() {
  const [recipientId, setRecipientId] = useState("");
  const [content, setContent] = useState("");
  const [messages, setMessages] = useState([]);
  const [users, setUsers] = useState([]);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await axios.get("/api/users");
        setUsers(res.data);
      } catch (err) {
        setError(err.response?.data?.message || "Failed to fetch users");
      }
    };

    const fetchMessages = async () => {
      try {
        const res = await axios.get("/api/messages");
        setMessages(res.data);
      } catch (err) {
        setError(err.response?.data?.message || "Failed to fetch messages");
      }
    };

    fetchUsers();
    fetchMessages();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post("/api/messages", { recipientId, content });
      setSuccess(res.data.message);
      setContent("");
      setRecipientId("");
      setError("");
      const updatedMessages = await axios.get("/api/messages");
      setMessages(updatedMessages.data);
    } catch (err) {
      setError(err.response?.data?.message || "Failed to send message");
      setSuccess("");
    }
  };

  return (
    <Container className="py-5">
      <div className="form-container">
        <h2 className="text-center mb-4">Messages</h2>
        {error && <Alert variant="danger">{error}</Alert>}
        {success && <Alert variant="success">{success}</Alert>}
        <Form onSubmit={handleSubmit} className="mb-4">
          <Form.Group className="mb-3">
            <Form.Label>Recipient</Form.Label>
            <Form.Select
              value={recipientId}
              onChange={(e) => setRecipientId(e.target.value)}
              required
            >
              <option value="">Select Recipient</option>
              {users.map((user) => (
                <option key={user._id} value={user._id}>
                  {user.name} ({user.email})
                </option>
              ))}
            </Form.Select>
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label>Message</Form.Label>
            <Form.Control
              as="textarea"
              rows={3}
              value={content}
              onChange={(e) => setContent(e.target.value)}
              placeholder="Type your message"
              required
            />
          </Form.Group>
          <Button variant="primary" type="submit" className="w-100">
            Send
          </Button>
        </Form>
      </div>
      <ListGroup>
        {messages.map((msg) => (
          <ListGroup.Item key={msg._id} className="mb-2">
            <Message
              sender={msg.senderId.name}
              recipient={msg.recipientId.name}
              content={msg.content}
              timestamp={msg.timestamp}
            />
          </ListGroup.Item>
        ))}
      </ListGroup>
    </Container>
  );
}

export default Messages;
